  <div class="content-body">
    <div class="container">
      <div class="col-xxl-12">
        <p>SVC Total max. supply: <strong>210 million</strong></p>
        <div>
          <h2>Reaperville Staking Statistics</h2>
          <h4 class="mt-4">Earnable reward tokens:</h4>
          <ul>
            <li>
              <strong>5 million in total for Reaperville</strong>
            </li>
            <li>
              <p>1 million in first year</p>
            </li>
            <li>
              <p>2.5 million in first 3 years</p>
            </li>
          </ul>
          <h4 class="mt-4">
            <p>
              Total staked power: <strong>{{ totalStakedPower }}</strong>
            </p>
            <p>
              Base staking power:
              <strong
                >{{ baseStakingPower }} ==
                {{
                  Math.round((baseStakingPower / totalStakedPower) * 10000) /
                  100
                }}%</strong
              >
            </p>
          </h4>
          <p>Stake and upgrade add 1% to base staking power</p>
          <p>Unstake adds 5% to base staking power</p>
          <p>Emergency unstake adds 0% to base staking power</p>
          <h4>
            <p>
              Reward tokens minted:
              <strong
                >{{
                  Math.round(
                    (5 * 10 ** 24 -
                      Number(rewardTokenMintAllowanceE0["reaper"])) /
                      10 ** 18
                  )
                }}
                SVC</strong
              >
            </p>
            <p>
              Reaperville total emission rate:
              <strong
                >{{ rewardTokenEmissionPerHourE18 }} SVC / hour @100%
                payout</strong
              >
            </p>
            <p>
              =
              {{
                Math.round((rewardTokenEmissionPerHourE18 * 24 * 365) / 1000) /
                1000
              }}
              million SVC / year
            </p>
          </h4>

          <hr class="mt-4 mb-4" />

          <h1 class="mx-auto mb-0 mt-4 animate-characters">
            Everything below is ADMIN ONLY
          </h1>
          <br />
          <h2 v-if="!userIsAdmin" class="mx-auto mt-0 mb-4 animate-characters">
            (You are not logged into the admin wallet)
          </h2>
          <h2 v-else class="mx-auto mt-0 mb-4 animate-characters">
            (YOU ARE THE ADMIN)
          </h2>

          <hr class="mt-4 mb-4" />

          <h2>Reward Token SVC</h2>
          Update allowance of {{ newMinterAddress }} to
          {{
            Math.round(rewardTokenMintAllowanceE0["newMinter"] / 10 ** 18) /
            10 ** 3
          }}
          k SVC =
          {{
            Math.round(rewardTokenMintAllowanceE0["newMinter"] / 10 ** 21) /
            10 ** 3
          }}
          M SVC
          <input
            type="text"
            class="form-control mx-auto"
            v-model="rewardTokenMintAllowanceE0['newMinter']"
          />
          <input
            id="newMinterAddress"
            type="text"
            class="form-control mx-auto"
            v-model="newMinterAddress"
          />

          <button
            class="btn btn-success"
            :disabled="!userIsAdmin"
            @click="getSvcMintAllowance('newMinter')"
          >
            Load allowance of {{ newMinterAddress }}
          </button>

          &nbsp;

          <button
            class="btn btn-danger"
            :disabled="!userIsAdmin"
            @click="
              setMintAllowanceByCa(
                newMinterAddress,
                rewardTokenMintAllowanceE0['newMinter']
              )
            "
          >
            Update allowance to
            {{
              Math.round(rewardTokenMintAllowanceE0["newMinter"] / 10 ** 21) /
              10 ** 3
            }}
            M SVC
          </button>

          <!-- &nbsp;

          <button
            class="btn btn-std"
            :disabled="!userIsAdmin"
            @click="mintRewardToken(newMinterAddress, rewardTokenMintAllowanceE0['newMinter'])"
          >
            Mint {{ rewardTokenMintAllowanceE0['newMinter'] }} SVC
          </button> -->

          <hr />

          <h2 class="mb-0">Card Contract</h2>
          <div>Initial allowance should be 90626000000000000000000</div>
          <div>
            Card contract can still mint
            <strong>
              {{ Math.round(rewardTokenMintAllowanceE0["cards"] / 10 ** 18) }}
            </strong>
            SVC =
            <strong>
              {{
                Math.round(rewardTokenMintAllowanceE0["cards"] / 10 ** 18) /
                10 ** 3
              }}
            </strong>
            k SVC
          </div>
          <input
            type="text"
            class="form-control mx-auto"
            v-model="rewardTokenMintAllowanceE0['cards']"
          />
          <div>
            <button
              class="btn btn-success"
              :disabled="!userIsAdmin"
              @click="getSvcMintAllowance('cards')"
            >
              Load allowance of cards contract
            </button>

            &nbsp;

            <button
              class="btn btn-danger"
              :disabled="!userIsAdmin"
              type="button"
              @click="
                setMintAllowance('cards', rewardTokenMintAllowanceE0['cards'])
              "
            >
              Set new mint allowance
            </button>

            &nbsp;

            <button
              class="btn btn-danger"
              type="button"
              :disabled="!userIsAdmin"
              @click="resetContract('cards')"
            >
              Reset contract for new round
            </button>
          </div>

          <br />

          <h4>Card Game Contract Balances</h4>
          <p>
            {{ Math.round(bchBalanceE0["cards"] / 10 ** 15) / 10 ** 3 }}
            BCH
          </p>
          <p>
            {{ Math.round(rewardTokenBalanceE0["cards"] / 10 ** 15) / 10 ** 3 }}
            SVC
          </p>
          <div>
            <button
              class="btn btn-std"
              type="button"
              :disabled="bchBalanceE0['cards'] <= 0 || !userIsAdmin"
              @click="withdrawBchFromContract('cards')"
            >
              withdraw BCH
            </button>
          </div>

          <br />

          <h2 class="mb-0">Reaperville Contract</h2>
          <div>
            Reaperville contract can still mint
            <strong>
              {{ Math.round(rewardTokenMintAllowanceE0["reaper"] / 10 ** 18) }}
            </strong>
            SVC =
            <strong>
              {{
                Math.round(rewardTokenMintAllowanceE0["reaper"] / 10 ** 21) /
                10 ** 3
              }}
            </strong>
            M SVC
          </div>
          <input
            type="text"
            class="form-control mx-auto"
            v-model="rewardTokenMintAllowanceE0['reaper']"
          />
          <div>
            <button
              class="btn btn-success"
              :disabled="!userIsAdmin"
              type="button"
              @click="
                setMintAllowance('reaper', rewardTokenMintAllowanceE0['reaper'])
              "
            >
              Update Reaper's mint allowance
            </button>
          </div>

          <br />

          <h4>Reaper Contract Balances</h4>
          <p>
            {{ Math.round(bchBalanceE0["reaper"] / 10 ** 15) / 10 ** 3 }}
            BCH
          </p>
          <div>
            <button
              class="btn btn-std"
              type="button"
              :disabled="bchBalanceE0['reaper'] <= 0 || !userIsAdmin"
              @click="withdrawBchFromContract('reaper')"
            >
              withdraw BCH
            </button>
          </div>

          <br />

          <h4>Reaper Mint Price</h4>
          <p>
            {{ Math.round(bchPerNftE0 / 10 ** 12) / 10 ** 6 }}
            BCH
          </p>
          <input
            id="bchPerNftE0"
            type="text"
            class="form-control mx-auto"
            v-model="bchPerNftE0"
          />
          <div>
            <button
              class="btn btn-std"
              :disabled="!userIsAdmin"
              type="button"
              @click="setBchPerNftE0('reaper')"
            >
              set new price
              {{ Math.round(bchPerNftE0 / 10 ** 12) / 10 ** 6 }} BCH
            </button>
          </div>

          <br />

          <h2 class="mb-0">Pause Reaper Mint</h2>
          Mint is active: {{ !mintPaused }}
          <p>
            <button
              @click="setNftMintPaused(true)"
              :disabled="mintPaused || !userIsAdmin"
              class="btn btn-danger"
            >
              Pause
            </button>
            &nbsp;
            <button
              @click="setNftMintPaused(false)"
              :disabled="!mintPaused || !userIsAdmin"
              class="btn btn-success"
            >
              Unpause
            </button>
          </p>

          <h2 class="mb-0">Pause Reaper Staking</h2>
          Staking is active: {{ !stakingPaused }}
          <p>
            <button
              @click="setNftStakingPaused('reaper', true)"
              :disabled="stakingPaused || !userIsAdmin"
              class="btn btn-danger"
            >
              Pause
            </button>
            &nbsp;
            <button
              @click="setNftStakingPaused('reaper', false)"
              :disabled="!stakingPaused || !userIsAdmin"
              class="btn btn-success"
            >
              Unpause
            </button>
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ethers } from "ethers";
import RewardToken from "../../artifacts/contracts/RewardToken.sol/RewardToken.json";
import Reaperville from "../../artifacts/contracts/Reaperville.sol/Reaperville.json";
import Cards from "../../artifacts/contracts/Cards.sol/Cards.json";

export default {
  name: "App",
  components: {},
  data() {
    return {
      contracts: {},

      newMinterAddress: "",

      userIsAdmin: false,

      // contracts
      bchBalanceE0: {},
      rewardTokenMintAllowanceE0: {},
      rewardTokenBalanceE0: {},

      // staking Reapers
      stakingPaused: false,
      totalStakedPower: 0,
      baseStakingPower: 0,
      rewardTokenEmissionPerHourE18: 0,

      // nft minting
      mintPaused: false,
      bchPerNftE0: "0",
    };
  },
  methods: {
    mintRewardToken: async function (newMinterAddress, amount) {
      let tx = await this.contracts["svc"].mint(
        newMinterAddress,
        ethers.utils.parseUnits(amount.toString(), "ether")
      );
      await tx.wait();
    },

    setNftMintPaused: async function (newPaused) {
      let tx = await this.contracts["reaper"].setMintPaused(newPaused);
      await tx.wait();
      window.location.reload();
    },

    setNftStakingPaused: async function (_contractName, _newPaused) {
      let tx = await this.contracts[_contractName].setStakingPaused(_newPaused);
      await tx.wait();
      window.location.reload();
    },

    setBchPerNftE0: async function (_contractName) {
      let tx = await this.contracts[_contractName].setBchPerNft(
        String(this.bchPerNftE0)
      );
      await tx.wait();
      window.location.reload();
    },

    withdrawBchFromContract: async function (_contractName) {
      let tx = await this.contracts[_contractName].withdrawBch();
      await tx.wait();
      window.location.reload();
    },

    resetContract: async function (_contractName) {
      let tx = await this.contracts[_contractName].reset();
      await tx.wait();
      window.location.reload();
    },

    setMintAllowanceByCa: async function (_address, _newAllowance) {
      let tx;
      if (_newAllowance === 0) {
        tx = await this.contracts["svc"].removeMinter(_address);
      } else {
        tx = await this.contracts["svc"].updateMintersAllowance(
          _address,
          _newAllowance
        );
      }
      await tx.wait();
      window.location.reload();
    },

    setMintAllowance: async function (_addressName, _newAllowance) {
      let _contractAddress =
        _addressName === "newMinter"
          ? this.newMinterAddress
          : window.ca[_addressName];

      this.setMintAllowanceByCa(_contractAddress, _newAllowance);
    },

    getSvcMintAllowance: async function (_addressName) {
      this.contracts["svc"]
        .remainingMintersAllowance(window.ca[_addressName])
        .then((_allowance) => {
          this.rewardTokenMintAllowanceE0[_addressName] = String(_allowance);
        });
    },

    getSvcBalance: async function (_addressName) {
      this.contracts["svc"]
        .balanceOf(window.ca[_addressName])
        .then((_balance) => {
          this.rewardTokenBalanceE0[_addressName] = String(_balance);
        });
    },

    getBchBalance: async function (_addressName) {
      this.provider.getBalance(window.ca[_addressName]).then((balance) => {
        this.bchBalanceE0[_addressName] = String(balance);
      });
    },
  },

  mounted: function () {
    this.provider = new ethers.providers.Web3Provider(window.ethereum, "any");

    this.provider
      .send("eth_requestAccounts", [])
      .then(() => {
        this.signer = this.provider.getSigner();

        this.contracts["svc"] = new ethers.Contract(
          window.ca["svc"],
          RewardToken.abi,
          this.signer
        );

        this.contracts["reaper"] = new ethers.Contract(
          window.ca["reaper"],
          Reaperville.abi,
          this.signer
        );

        this.contracts["cards"] = new ethers.Contract(
          window.ca["cards"],
          Cards.abi,
          this.signer
        );

        // this.flexUsdTokenContract = new ethers.Contract(
        //   window.FlexUsdAddress,
        //   RewardToken.abi,
        //   this.signer
        // );
      })
      .then(() => {
        this.signer.getAddress().then((address) => {
          if (address === window.ca["dev"]) {
            this.userIsAdmin = true;
          }

          this.newMinterAddress = address;

          this.getSvcMintAllowance("newMinter");
        });

        this.contracts["reaper"]
          .showStakerInfo(window.ca["dev"])
          .then((stakerInfo) => {
            this.totalStakedPower = stakerInfo[2];
            this.baseStakingPower = stakerInfo[3];
            this.rewardTokenEmissionPerHourE18 = String(
              Math.round(ethers.utils.formatEther(stakerInfo[4]) * 100000) /
                100000
            );
          });

        // token balances
        const _contractsToLoad = ["reaper", "cards"];
        for (let _contract of _contractsToLoad) {
          this.getBchBalance(_contract);
          this.getSvcBalance(_contract);
          this.getSvcMintAllowance(_contract);
        }

        this.contracts["reaper"].isMintPausedStakingPaused().then((result) => {
          this.mintPaused = result[0];
          this.stakingPaused = result[1];
        });

        this.contracts["reaper"].bchPerNft().then((_bchPerNft) => {
          this.bchPerNftE0 = String(_bchPerNft);
        });
      });
  },
};
</script>
